/*eslint-disable*/module.exports={messages:{"404 - Siden finnes ikke":"404 - Page not found","<0>Dette er ikke første gang jeg har lagd en egen portefølje. Under kan du se mine tidligere, som jeg aldri ble helt fornøyd med.</0>":"<0>This isn't the first time I've made my own portfolio. Below you can se the earlier versions, which I never felt quite satisfied with.</0>","<0>I 2013 hadde jeg en-ukes praksis hos Orega på Lade i Trondheim. Dette var mens jeg gikk interiør og utstillingsdesign på VGS. Der fikk jeg være med på å designe logo for Junior NM på Ski 2015. Det var kjempekult!</0><1>Leveransen min på slutten av uka ble bokstavene «NM» slik som de er i den ferdige logoen. Jeg lagde en sort-hvitt versjon og mange forskjellige fargevariasjoner av den, deriblant den med norskeflagg-fargene. Under «NM»-bokstavene hadde jeg skrevet «Jr NM på ski 2015» med grå tekst.</1><2>I den logoen som ble brukt i arrangementet hadde den grå teksten heldigvis blitt byttet ut med en blå tekst (bilde), som ser mye bedre ut. Mistenker også at noen har vektorisert den, siden logoen så mye renere ut da jeg så at den på arrangementet.</2><3>Til å designe logoen brukte jeg Photoshop. I ettertid har jeg skjønt at jeg heller burde ha brukt et vektorprogram som Illustrator, men dette visste jeg ikke da. En konsekvens av å bruke Photoshop var at ikke alle piksler ville henge med når man endrer farge med paint-bucket-tool. Jeg prøvde ut logoen i masse forskjellige farger, og man kunne se restepiksler fra tidligere fargefyll i de forskjellige utkastene.</3>":"<0>In 2013 I had a one-week internship at Orega in Lade, Trondheim. Here I was set to design a logo for Junior NM on ski 2015.</0>","<0>I 2015 hadde jeg et valgfritt prosjekt på videregående hvor jeg valgte å redesigne <1>alistairmaclean.com</1> sitt GUI. Grunnen var at jeg tidligere hadde jeg sett og vært skuffet over hvordan den så ut. MacLean skrev nesten utelukkende bøker med action og spenning, og det syntes jeg ikke nettsiden reflekterte noe av.</0><2>Verktøy</2><3>Jeg bygget nettsiden i HTML og CSS og bittelitt JavaScript (ikke så mye å skryte av). Logoen lagde jeg i Illustrator og header-bildet lagde jeg ved photoshoppe et bilde av eget modellfly oppå noen T-skjorter.</3>":"<0>In 2015 I had a project on videregående (equivalent to high school) where I chose to redesign the GUI of <1>alistairmaclean.com</1>. The reason behind this, was that I had previously seen and been dissapointed by it. MacLean almost exclusively books with action and suspense, and I didn't think this site resembled that the slightest.</0><2>Tools</2><3>I built the site using HTML and CSS and a little bit JavaScript. I made the logo in Illustrator and the header image in Photoshop.</3>","<0>Jeg jobbet i en gruppe med to andre. Jeg arbeidet mest med videoopplasting, ‑redigering og ‑visning. I tillegg gjorde jeg det spilleliste relaterte. Teknisk sett innebar dette å jobbe med appens interaksjon med databasen, databasestrukturen og nedlasting av videoer til filsystemet. Jeg jobbet også litt med kommentarer.</0><1>Appen ble lagd med PHP-filer og templating engine'en Twig for å rendre html-sidene.</1>":"<0>In this I worked in a group with two others. My responsibilities was most on video uploading, -editing and -playing. Additionaly I did the playlist related stuff. Technically speaking this meant working with the app's integration with the database, database structure and downloading of videos to the filesystem. I also worked a little with the comments.</0><1>The app was made with PHP-files and the templating engine Twig to render the html-pages.","<0>Jeg startet på denne siden desember 2020, og jeg designet den spesifikt for å presentere webprosjekt jeg hadde gjort. Det skulle fungere som et supplement til CV i forbindelse med jobbsøking. I senere tid har jeg lagt til mer innhold og forbedret etter SEO. Jeg er veldig fornøyd med designet, kodekvaliteten og valg av verktøy.</0><1>Hvorfor React?</1><2>Før jeg brukte React til porteføljen brukte jeg JavaScript til å etterligne en komponent-basert arkitektur. Det var fordi noen elementer måtte gjenbrukes på hver side (Nav og Footer) og et element måtte ryddes bort fra index.html (søylediagrammet).</2><3>Å lage komponenter med \"vanilla JavaScript\" ble ganske uelegant etter hvert som koden ble større. Jeg gikk derfor over til React da det var det komponent-baserte verktøyet jeg hadde tidligere erfaring med.</3>":"<0>I started making this December 2020, it was made to present the web projects that I've made. It was to be used in job searching. Eventually I added more content and improved SEO. I'm very satisfied with its design, code quality and choice of web development tools.</0>","<0>Kompetanse</0><1>Jeg har erfaring innen webutvikling og grafisk design.</1>":"<0>Competence</0><1>I have experience in web development and graphic design.</1>","<0>Kravspesifikasjonen var mye det samme som i prosjekt 1. Her skulle det også skilles mellom ulike brukertyper, som var <1>admin</1>, <2>lærer</2> og <3>studenter</3>, hver med ulike rettigheter:</0><4><5><6>studenter</6> skulle ha mulighet til å se på videoer, kommentere, lagre video i spillelister etc.</5><7><8>lærere</8> skulle ha studenters rettigheter pluss mulighet til å legge ut videoer</7><9><10>admin</10> skulle ha de samme rettighetene som alle andre, pluss mulighet til å endre å brukertypen til de andre brukerne. (Når folk skal registrere seg som lærer, må de opprette en studentbruker, og så sende forespørsel til admin om å få byttet brukertype)</9></4><11>Nettappen skulle også være en <12>Single page application</12>, men dette fikk vi til bare delvis. (Hvis man klikker på en video i oversikten, tar siden en automatisk refresh. Man må også refreshe etter å ha lastet opp video eller gjort andre permanente endringer.)</11><13>I dette prosjektet jobbet jeg med én annen. Det jeg hadde ansvar for, var å få til videoopplasting, ‑visning og ‑oversikt. Jeg lagde også mesteparten av brukerredigeringsfunksjonen for admin-brukere. Jeg drev også med litt CSS styling.</13><14>Appen ble lagd med bruk av LitElement-baserte komponenter som vi for det meste lagde selv.</14>":"<0>The assignment specification was much like that of the first project. In this assignment we had to make different user types, which was <1>admin</1>, <2>teacher</2> og <3>students</3>, each with their own set of permissions:</0><4><5><6>students</6> should be able to wach videos, comment, insert video into playlists etc.</5><7><8>teachers</8> should have the same permissions as students, plus ability to upload videos</7><9><10>admin</10> should have all the persmissions of the preceding roles, plus permission to change the user type for other users. (To registrate a teacher user, one has to start with a student account, and then send a wants-to-get-teacher-user request to admin.)</9></4><11>The web app should also be a <12>Single page application</12>.</11><13>In this project I worked with one other student. My responsibility was to make video uploading, -playing and ‑overview functionality. I also made most of the user editing functionality for admins. I also did some CSS styling.</13><14>The app was made using LitElement-based components that we for the most part made ourselves.</14>","<0>Litt om Dokker</0><1>Dokker er en kommunikasjonsplatform for studenter og lærere i skole og lignende. I Dokker kan f.eks. lærere legge ut oppgaver, og elever kan levere inn besvarelser og selvvurderinger. <2/>Elevene får samlet opp dokumentasjon av egen kunnskap på Dokker, og dette kabn de vise frem til fremtidige arbeidsgivere. Det er et selling point at de behoolder tilgangen på dokumentasjonen livet ut. De kan også legge ut vitnemål og sertifikater.</1>":"<0>Some info about Dokker</0><1>Dokker is a communication platform for students, teachers in schools and the like. In Dokker teachers can eg. publish assignments, to which students can submit their work and also submit self-assessment & ranking of their own work. <2/>As the students gradually uses the app, they gather different documentation of their experience, which can be assignment feedback, uploaded diplomas or certificates. One major selling point in Dokker is that the students will still have access to their Dokker user and their experience documentation, even after they've finished the school were they achieved said experience.</1>","<0>Nettsider</0><1>Her er noen nettsider som jeg er stolt av å ha laget selv / bidratt på og som jeg har skrevet litt om.</1>":"<0>Web pages</0><1>Here's some web pages I'm proud of having made myself / contributed to.</1>","<0>Porteføljene jeg hadde lagd før denne (se under) var enten for mangelfull av informasjon om mitt arbeid eller hadde for dårlig kode, så da begynte jeg heller på denne. I denne forsøkte jeg å dekke så mye som mulig av det arbeidet jeg hadde gjort, alt grafisk og koderelatert.</0><1>Denne lagde jeg i PHP, mest for å løse for å fikse «samme-header-og-footer-på-alle-sidene». Jeg kunne ikke JavaScript så godt på denne tiden, og jeg hadde tilgang på en <2>live server</2> ­ (folk.ntnu.no/paalsst) som støttet PHP. Så det er grunnen til at det ikke ble en statisk side.</1>":"<0>The portfolios I made before this one (see below) was either too deficient of showcasing my web work, too ugly or its code was too bad. Therefore I scrapped them and began this one. In this portfolio I tried to cover as much as possible of the web and graphic work I had done.</0>","<0>Under denne fem-måneders praksisen bidro jeg i utviklingen av web-applikasjonen Dokker, produktet til Learning Moment. Jeg tok også et React kurs for å få den nødvendige kunnskapen jeg trengte. Jeg fikk bidratt med:</0><1><2>Nye komponenter</2><3>Refaktorering</3><4>Vurdering av andres kode</4><5>Litt bug-fixing</5></1><6>Under denne perioden lærte jeg:</6><7><8>React og relaterte verktøy</8><9>Samarbeid på prosjekt med bruk av <10>issues</10> og <11>pull requests</11></9></7><12>Komponentener jeg lagde</12>":"<0>During this five months internship I contributed to Dokker, an web application by Learning Moment. I also took a React course to get the necessary React development competence. I got to contribute with:</0><1><2>New components</2><3>Refactoring</3><4>Reviews of other's code</4><5>Some bug-fixing</5></1><6>During this practice I learned:</6><7><8>React and related technologies</8><9>Cooperation with the use of Github <10>issues</10> and <11>pull requests</11></9></7><12>Components I've made</12>","<0>Våren 2020, i faget WWW-teknologi, hadde vi to prosjekter hvor vi skulle lage nettapplikasjoner for videoopplasting og ‑visning. Altså mye det samme som det f.eks. Youtube, Udemy og Edx tilbyr.</0><1>Begge nettappene skulle gi brukere mulighet til å blant annet: laste opp videoer, se på dem, legge til et interaktivt transkript ved siden av videoen, endre videodetaljene, legge dem til i spillelister og abonnere på spillelister.</1><2>Det som skilte prosjektene, var verktøyene vi skulle bruke og noe av kravspesifikasjonen.</2><3>Første prosjekt</3>":"<0>In Spring 2020, in the course WWW-technologies we had two projects where we set to make web apps for uploading and watching videos. That is, replicate much of the functionality that you will find in services such as Youtube, Udemy and Edx.</0><1>Both web apps should make users able to inter alia upload videos, watch them, add interactive transcript, change video details (title, description etc.), place videos in playlists and subscribe to playlists.</1>","Alistair MacLean nettside redesign":"Alistair MacLean webpage redesign","Annet":"Other","Artistprofil":"Artist profile","Automatisk testing":"Automatic testing","Behersker godt":"Masters well","Behersker {tech} godt":["Masters ",["tech"]," well"],"Beskrivelse":"Description","Beste bøker listeeksempel":"Best books list example","Beste metroidvanias listeeksempel":"Best metroidvanias list example","Besøk siden":"Visit site","Bidrag":"Contribution","Bok web-banner":"Book web-banner","Bokcover o.l.":"Book cover and the like","Bosatt i Trondheim":"Resident in Trondheim","Bygd noen sider på den nye nettsiden (en Progressive Web App)":"Built some sites on the new webpage (a Progressive Web App)","Denne kom jeg aldri langt med før jeg la prosjektet fra meg. Den har ingen undersider og index-siden er halvferdig.":"This site was abandoned half-finished and has no sub-pages.","Denne porteføljen":"This portfolio","Dette er et bokbanner jeg lagde av Flavia de Luce-serien, en krim og mysterier-bokserie som tar plass i et 50-talls england. Hovedpersonen er en 12-år gammel kjeminerd, og det ville jeg få frem i dette banneret. Bokbanneret er ment for å passe til å bli brukt på en bokhandel-nettside, ark.no, tror jeg. (Det er fortsatt bare en skoleoppgave, ikke et 'ordentlig' oppdrag, altså)":"This is a book banner I made featuring Flavia de Luce, a crime and mystery book series that takes place in England in the fifties.","Dokker appen":"Dokker app","Dokker blogg":"Dokker blog","Du er på siden":"You are on the site","Du finner meg på:":"I'm available on:","Eksamensinnlevering i webkoding<0>2016 høst</0>":"Exam delivery in web coding<0>2016 fall</0>","En desktopside":"A desktop page","En nettside som jeg førte opp webkoding-notater i. Sist oppdatert 02.11.16":"A webpage that I made to write down subject matter in the course web coding. Last updated at 2016 November 2.","En portefølje påbegynt like før studiene<0>2016</0>":"A portfolio started just before I started my studies<0>2016</0>","Erfaring":"Experience","Et HTML templating språk (HTML som aksepterer variabler og expressions). Det er ikke så ulikt Twig og Handlebars.":"A HTML templating language (HTML that accepts variables and expressions). It's not so different to Twig and Handlebars.","Et bokcover jeg lagde for boka \"Mord på orientekspressen\" av Agatha Christie. Jeg lagde også et poster og andre design med samme motiv.":"A book cover I made for the book \"Murder on the Orient Express\" by Agata Christie. I also made a poster and some other designs.","Et kort for keep-up-delen av dashboard":"A card for the keep-up part of dashboard","Et kort for recent-activities-delen av dashboard":"A card for the recent-activites part of dashboard","Et poster jeg også lagde":"A poster which I also made","Extra Optical forside":"Extra Optical front page","Fagstoff":"Subject matter","Fant ingen bilder":"Found no images","Fikset errors og kodeslurv i HTML. For eksempel var det mange enslige <0>opening/closing</0>-HTML-tags som manglet <1>matching pair</1>.":"Fixed errors and code negligence in HTML. For example there were many <0>opening/closing</0>-HTML-tags that missed matching pair","Fikset layout på bunntekst og ordnet en side for mobilstørrelser":"Fixed layout on bottom text and made a site available for mobile sizes","Fikset på Extra Opticals Zendesk Help center.":"Fixed Extra Optical's Zendesk Help center","Fritidsprosjekt etter studiene<0>2020 desember–nå</0>":"Spare time project made after the studies<0>2020 December–now</0>","Fritidsprosjekt som aldri ble fullført":"Spare-time project which I never finished","Fritidsprosjekt under studiene<0>2019</0>":"Spare time project made during my studies<0>2019</0>","Fritidsprosjekt<0>2016 høst</0>":"Spare time project<0>2016 Fall</0>","Få pene og oversiktlige lister":"Get nice and pretty lists","Ganske god på":"Pretty good at","Ganske god på {tech}":["Pretty good at ",["tech"]],"Grafisk":"Graphics","Grafisk design":"Graphic design","Grafisk skoleoppgave<0>2015?</0>":"Graphic school assignment<0>2015?</0>","Grafisk skoleoppgave<0>2016</0>":"Graphic school assignment<0>2016</0>","Grunn­leggende":"Basics","Hjem":"Home","I 2015 gikk jeg på <0>medier og kommunikasjon</0> på Byåsen VGS. Der hadde jeg en oppgave i å lage en tresiders portefølje om meg selv. Etter å ha levert denne oppgaven endret jeg litt på layout-et.":"In 2015 i attended <0>media and communication</0> on Byåsen VGS (highschool), were I got the task to make a three-page portfolio about myself. After delivering this I improved it in my spare time.","I dennne praksisen jobbet jeg med nettbutikken Extra Optical (frontend). EO skulle gå over til å bygge nettsiden sin på nytt i en <0>page builder</0> da jeg hadde vært der i ca. 2 måneder, så jeg har fått jobbet på både ny og gammel versjon.":"In this internship i worked on the online store Extra Optical (frontend). EO was about to re-rebuild their webpage in a page builder when I had been there in ca. 2 months, which means I got to work on two versions of the site.","I faget \"grafiske verktøy, prinsipper og metoder\" skulle vi lage en klikkbar hi-fi prototype for en app. Appen skulle være musikkrelatert, så det endte opp med å bli en app for privatpersoner og musikkartister som kunne avtale spillejobber med hverandre. Jeg bestemte meg til å lage mobil- og desktop-visning av alle sidene.":"In the course \"Graphic Tools, Principles and Methods\" we made a clickable hi-fi prototype for an app. Appen skulle være musikkrelatert, så det endte opp med å bli en app for privatpersoner og musikkartister som kunne avtale spillejobber med hverandre. Jeg bestemte meg til å lage mobil- og desktop-visning av alle sidene.","Ikke tilgjengelig live":"Unavailable live","Innlevering på VGS og videreutvikling etter det<0>2015</0>":"Assignment at VGS and further development after that<0>2015</0>","Innlevering på videregående<0>2015</0>":"Assignment at high school<0>2015</0>","Innleveringsoppgave på medier og kommunikasjon":"Assignment at media and communication","Kan grunnleggende {tech}":["Know the basics of ",["tech"]],"Kan litt {tech}":["Know some ",["tech"]],"Kildekode":"Source code","Kunst":"Art","Lag egne lister":"Create your own lists","Lagd undersider":"Made subpages","Laster inn...":"Loading...","Logo for Junior NM på ski 2015":"Logo for Junior NM on ski 2015","Logo som ble brukt: En bearbeidet versjon av det jeg leverte i 2013":"Logo that was used","MySQL & relasjonsdatabaser":"MySQL & relational databases","MySQL & relasjons­databaser":"MySQL & relational databases","Nettside med overskriften 'fagstoff'":"Webpage with its heading reading 'subject matter'","Nettsider":"Websites","Oversikt over lister eksempel":"Overview of lists example","Plakat":"Poster","Praksis hos Extra Optical<0>2021 november–nå</0>":"Internship at Extra Optical<0>2021 November–now</0>","Praksisplass<0>2013</0>":"Internship<0>2013</0>","Praksisprosjekt hos Learning Moment Technologies<0>2021 april–september</0>":"Internship project at Learning Moment Technologies<0>2021 April—September</0>","Privat kildekode":"Private source code","Prosjekt i studiene<0>2019 høst</0>":"Project at university<0>2019 fall</0>","Prosjektet er foreløpig satt på pause.<0>I slutten av juli 2020 begynte jeg å lage denne webappen for å rangere ting, hvor poenggivingen er en sortering fra best til dårligst. Jeg vet ikke hvor mye jeg vil klare å legge til denne webappen, men det hadde vært gøy om brukerne kunne dele/vise listen sin med andre brukere, bidra på felles lister og importere listelementer direkte fra andre databaser (med web API).</0><1>Motivasjon til prosjektet</1>Kort sagt: Jeg ville prøve å lage det ranker.com burde ha vært. Og for å ha et kodeprosjekt å drive på med.<2>Ranker.com er en nettapp som hadde mye av den samme funksjonaliteten som jeg nevnte over. Jeg likte det, og det var derfor skuffende da ranker.com plutselig på et tidspunkt bestemte seg for å skrote konseptet sitt. (De byttet ut å ordne lister mot å kun velge én favoritt per kategori.) Nettsidedesignet syntes jeg allerede ikke var særlig pent. Det var dette, samt behovet mitt for å holde på med noe koding etter å ha blitt ferdig med studiene, som fikk meg til å begynne dette prosjektet.</2><3>Det skal nevnes at ranker.com i ettertid har ombestemt seg og gått tilbake til sitt originale konsept igjen. Men nettsiden er fortsatt et stort rot.</3><4>Uansett, jeg hadde jo blitt bedre på programmering i JavaScript, så det i seg selv er jo motivasjon nok til å fortsette med det.</4>":"This project is currently in hiatus<0>At the end of July 2020 I began to make this web app for ranking stuff. I don't know how much I will be able to add to this app, but it would have been nice if the users could share/show their lists with other's, contribute to a public shared list and import list elements directly from web API</0><1>Motivation for this project</1> I wanted to make what ranker.com should have been, and because I wanted to just have a web project to work on.","Ranking app<0>2020 høst–2021 september</0>":"Ranking app<0>2020 fall—2021 September</0>","React sertifikat":"React certificate","Relasjonsdatabase":"Relational database","Rettet opp / fjernet <0>broken links</0>":"Corrected / removed broken links","Se mer på Github":"See more on Github","Sett inn egne elementer, tilpass etter behov":"Insert your own elements, customize to your needs","Sist oppdatert 16. januar 2022. Innholdet er litt utdatert.":"Last updated at 16th of January 2022. The contents of this page is a little outdated.","Startside":"Front page","Søkefelt":"Search bar","Søkeresultater":"Search results","To studie­proskekter<0>2020 vår</0>":"Two university projects<0>2020 spring</0>","Utdannet web­utvikler":"Educated web developer","Utkast og skisser til web-banneret oppe til venstre. Jeg har desverre ikke tatt vare på et mer høyoppløselig bilde av det.":"Drafts and sketches for the web-banner top left.","Verktøy for å bygge komponent-baserte nettsider. LitElement skiller seg fra React ved at den bruker HTML som sitt templating språk (og ikke JSX). Ellers så har LitElement kun <0>class-based</0> components.":"Tool for creating component-based webpages. LitElement differs from React by using HTML templating literals as its templating language (and not JSX). LitElement has only class-based components.","Verktøy:":"Tools:","Videooversikt":"Video overview","Videoside i andre prosjekt":"Video page in second project","Videoside i første prosjekt":"Video page in first project","Video­opplastings­apper":"Video uploading apps","Videre lesing":"Further reading","Videreutvikling etter leveranse: nye farger og noe endret layout":"Further development after delivery: added new colors and changed some of the layout","Webutvikling":"Web development","_Sammenheng<0>_semester</0>":"eeeee","_bilde":"_img","_bilde1":"_img1","_bilde2":"_img2","bokcover":"book cover","flere bilder med flavia de luce motiver":"multiple pages with flavia de luce motives","maclean nettside":"maclean webpage","mord på orienteksekspressen bokcover":"murder on the Orient Express book cover","plakat med logo":"poster with logo","portefølje 1 versjon 1":"portfolio 1 version 1","portefølje 1 versjon 2":"portfolio 1 version 2","portefølje 2":"portfolio 2","portefølje 3":"portfolio 3","portefølje 4":"portfolio 4","portefølje 5":"portfolio 5","utkast":"drafts","utkast til bokcover":"drafts to book cover","videolistesiden":"video list page","videoopplastingssiden":"video upload page","videosiden i andre prosjekt":"video page in other projects","videosiden i første prosjekt":"video page in first project","«Last opp video»-siden":"“upload video”-page","Åpne Udemy siden for dette sertifikatet":"Open the Udemy page for this certificate"}};